import React, { ReactNode } from "react";
import { Link } from "react-router-dom";
import Cart from "src/components/cart";
import NotLoadedCarts from "src/components/notLoadedCarts ";
import QuestionLabel from "src/components/question";

interface PropsType {
  carts?: Array<any>;
  onLike: any;

  description?: string;
  title?: string;
  isShowCarts: boolean;
  isShowText: boolean;
  isShowLables: boolean;
  onAdd: any;
  loading: any;
}
const PageTemplate: React.FC<PropsType> = ({
  loading,
  carts,
  onAdd,
  onLike,
  isShowLables,
  isShowText,
  isShowCarts,
  title,
  description,
}) => {
  const showCarts = (): ReactNode[] => {
    return carts
      ? carts.map((cart, index) => (
          <Link
            aria-label={"  " + cart.name}
            to={`/${cart.id}`}
            key={cart.id}
          >
            <Cart onAdd={onAdd} carts={carts} cart={cart} onLike={onLike} />
          </Link>
        ))
      : [];
  };

  return (
    <>
      <div className="page__wrapper">
        {isShowCarts ? loading ? <NotLoadedCarts /> : showCarts() : null}
      </div>
      <div className="page__container">
        {isShowLables ? (
          <>
            <QuestionLabel
              title="  "
              description={` ,        . <br/>   ,     ,      (    )     -       - .<br/><br/>    ,   ...    ,    <a href ='https://t.me/mister_gifter'></a>,  `}
            />
            <QuestionLabel
              title="  "
              description={`  18:00      !<br/>
           1-3 `}
            />
          </>
        ) : null}
        {isShowText ? (
          <>
            <h2 className="page__title">{title}</h2>
            <p className="page__description">{description}</p>
          </>
        ) : null}
      </div>
    </>
  );
};
export default PageTemplate;
